<template>
  <div class="first-layout">
    <iframe ref="unity" :src="unitySrc" class="simulation-iframe" @load="onLoad"></iframe>
  </div>
</template>

<script>
//实验仿真
import { getAbsoluteUrl } from '@/assets/js/api';

export default {
    name: 'Simulation',
    data() {
        return {
            unitySrc: getAbsoluteUrl('unity/index.html')
        };
    },
    methods: {
        onLoad() {
            let wid = this.$refs.unity.contentWindow;
            //导出的数据
            wid.ExportData = (data) => {
                this.$store.commit('setScore', data);
                this.$store.commit('setDidExp', true);
            };
        }
    },
};
</script>